export default class HashMap extends Map {

    constructor(hash) {
        super();
        this.hash = hash;
    }

    set(key, value) {
        return super.set(this.hash(key), value);
    }

    get(key) {
        return super.get(this.hash(key));
    }

    has(key) {
        return super.has(this.hash(key));
    }

    delete(key) {
        return super.delete(this.hash(key));
    }

}