export default class Info {

    constructor() {
        this.ctx = document.getElementById("info").getContext("2d");
        this.displayText();
    }

    displayText() {
        this.ctx.fillStyle = "black";
        this.ctx.font = "bold 32px Georgia";
        this.ctx.fillText("Objetivo del juego", 75, 35);
        this.ctx.font = "15px Georgia";
        this.ctx.fillText("Este juego consiste en rellenar el 75% de la pantalla formando polgonos.", 75, 70);
        this.ctx.fillText("Un polgono es completado al conectar la lnea con una pared ya existente.", 75, 90);
        this.ctx.fillText("Si el enemigo principal, el Qix, choca contigo o cualquier punto de un polgono ", 75, 110);
        this.ctx.fillText("sin completar, morirs. Lo mismo ocurrir si un Sparx te toca.", 75, 130);
        this.ctx.font = "bold 32px Georgia";
        this.ctx.fillText("Controles", 75, 180);
        this.ctx.font = "bold 15px Georgia";
        this.ctx.fillText("Flechas direccionales:", 75, 215);
        this.ctx.font = "15px Georgia";
        this.ctx.fillText("Movimiento del jugador.", 250, 215);
        this.ctx.font = "bold 15px Georgia";
        this.ctx.fillText("Shift:", 75, 235);
        this.ctx.font = "15px Georgia";
        this.ctx.fillText("Permite comenzar a crear un polgono.", 122, 235);
        this.ctx.font = "bold 15px Georgia";
        this.ctx.fillText("'P' y 'p':", 75, 255);
        this.ctx.font = "15px Georgia";
        this.ctx.fillText("Pausar o reanudar el juego.", 138, 255);
        this.ctx.font = "bold 12px Georgia";
    }

    displayFPS() {
        this.ctx.clearRect(70, 450, 70, 20);
        this.ctx.fillText("FPS: " + game.fps, 75, 460);
    }

}