import {
    State
} from './state';

export default class Player {

    constructor(grid) {
        this.FillScore = [0, 5, 10];
        this.grid = grid;
        this.ctx = grid.ctx;
        this.position = [96, 118];
        this.dx = 0;
        this.dy = 0;
        this.dir = [this.dx, this.dy];
        this.draw = 0;
        this.drawing = 0;
        this.launch_point = null;
        this.score = 0;
    }

    add_score(n) {
        this.score += n;
    }

    // Movimiento del Player
    update() {
        let dx, dy, draw;
        let [x, y] = this.position;
        if (((x + y) & 1) === 0) { // Suma par, se puede girar
            [dx, dy, draw] = this.calc_dir();
            if (this.drawing) {
                if (this.grid.cell_empty(x + dx * 2, y + dy * 2) || this.grid.cell_wall(x + dx * 2, y + dy * 2) && ((x + dx * 2) !== this.launch_point[0] || (y + dy * 2) !== this.launch_point[1])) {
                    this.dir = [dx, dy];
                    x += dx;
                    y += dy;
                    this.grid.set(x, y, this.TRAIL);
                    this.position = [x, y];
                } else {
                    this.dir = [0, 0];
                }
            } else {
                if (this.grid.cell_wall(x + dx, y + dy)) {
                    this.dir = [dx, dy];
                    x += dx;
                    y += dy;
                    this.position = [x, y];
                } else if (draw && this.grid.cell_empty(x + dx, y + dy) && /* Empezar a pintar */
                    this.grid.cell_empty(x + dx * 2, y + dy * 2) || (draw && this.grid.cell_wall(x + dx * 2, y + dy * 2) && this.grid.cell_empty(x + dx, y + dy))) {
                    this.launch_point = [x, y];
                    this.dir = [dx, dy];
                    x += dx;
                    y += dy;
                    this.position = [x, y];
                    this.drawing = draw;
                    this.grid.set(x, y, this.TRAIL);
                } else { // Direccion bloqueada
                    this.dir = [0, 0];
                }
            }
        } else { // Posicion impar, no se puede girar. Forzar a moverse a la siguiente casilla.
            [dx, dy] = this.dir;
            if (this.drawing) {
                if (this.grid.cell_wall(x + dx, y + dy)) {
                    // landing
                    x += dx;
                    y += dy;
                    //  corner points
                    pa = [x - dx - dy, y - dy - dx];
                    pb = [x - dx + dy, y - dy + dx];
                    if (this.grid.cell_wall(x - dy, y - dx)) {
                        da = [-dy, -dx];
                    } else {
                        da = [dx, dy];
                    }
                    if (this.grid.cell_wall(x + dy, y + dx)) {
                        db = [+dy, +dx];
                    } else {
                        db = [dx, dy];
                    }
                    this.grid.trace([x, y], this.launch_point, [-dx, -dy], this.TRAIL, this.TRAILTRACE);
                    this.grid.trace([x, y], this.launch_point, da, this.WALL, this.WALLTRACE);
                    let test_point = Qix.p[0];
                    if (this.grid.seek(pa, test_point, [10, 7])) {
                        n = this.grid.fill(pb, [10, 7], 2);
                        this.add_score(n * this.FillScore[1]);
                        if (this.grid.percent > 75) {
                            this.add_score(Math.round(this.grid.percent - 75) * this.FillScore[2]);
                            game.won = true;
                            game.ended = true;
                        } else if (this.grid.percent === 75) {
                            game.won = true;
                            game.ended = true;
                        }
                        this.grid.current_filling = n;
                        this.grid.trace([x, y], this.launch_point, da, 11, this.WALL); // # rewall
                        this.grid.trace([x, y], this.launch_point, db, this.WALL, 4); // # bury
                        this.grid.trace([x, y], this.launch_point, [-dx, -dy], 10, this.WALL); // # solidify
                    } else {
                        let [xa, ya] = this.launch_point;
                        this.grid.set(xa, ya, 11);
                        this.grid.set(x, y, 11);
                        n = this.grid.fill(pa, [10, 11], 2);
                        this.grid.current_filling = n;
                        this.add_score(n * this.FillScore[1]);
                        if (this.grid.percent > 75) {
                            this.add_score(Math.round(this.grid.percent - 75) * this.FillScore[2]);
                            game.won = true;
                            game.ended = true;
                        } else if (this.grid.percent === 75) {
                            game.won = true;
                            game.ended = true;
                        }
                        this.grid.set(xa, ya, 7);
                        this.grid.set(x, y, 7);
                        this.grid.trace([x, y], this.launch_point, da, 11, 4); // # bury;
                        this.grid.trace([x, y], this.launch_point, [-dx, -dy], 10, 7); //  # solidify;
                    }
                    this.drawing = 0;
                    this.launch_point = null;
                } else {
                    x += dx;
                    y += dy;
                    this.grid.set(x, y, this.TRAIL);
                }
            } else {
                x += dx;
                y += dy;
            }
        }
        this.position = [x, y];
        this.grid.dirty_region(x, y, 3);
    }

    // Pintar al Player
    paint() {
        this.ctx.beginPath();
        this.ctx.strokeStyle = "black";
        this.ctx.arc(this.grid.offset[0] + (this.position[0] * 3), this.grid.offset[1] + (this.position[1] * 3), 5, 0, 2 * Math.PI, true);
        this.ctx.stroke();
        this.ctx.fillStyle = "red";
        this.ctx.fill();
    }

    // Calcular la direccion que debe seguir segun los input
    calc_dir() {
        let dx = 0,
            dy = 0,
            draw = 0;
        if (State.keys.shift) {
            draw = 1;
        } else {
            draw = 0;
        }
        if (State.keys.left) {
            dx = -1;
            dy = 0;
        } else if (State.keys.right) {
            dx = 1;
            dy = 0;
        } else if (State.keys.up) {
            dx = 0;
            dy = -1;
        } else if (State.keys.down) {
            dx = 0;
            dy = 1;
        } else {
            dx = 0;
            dy = 0;
        }
        return [dx, dy, draw];
    }

}

Player.EMPTY = 0;
Player.TRAIL = 5;
Player.WALL = 7;
Player.OUT = 3;
Player.TRAILTRACE = 10;
Player.WALLTRACE = 11;