import Enemy from './enemy';
import Grid from './grid';
import Info from './info';
import Player from './player';
import Qix from './qix';
import Resources from "./resources";
import {
    State
} from './state';

export default class Game {

    constructor() {
        this.info = new Info();
        this.grid = new Grid();
        this.qix = new Qix();
        this.player = State.player = new Player(this.grid);
        this.loadAssets();
    }

    loadAssets() {
        State.soundtrack = new Howl({
            src: ['/sounds/soundtrack.m4a'],
            volume: 0.25,
        });
        State.gameWon = new Howl({
            src: ['/sounds/victory.mp3'],
            volume: 0.25,
        });
        State.gameLost = new Howl({
            src: ['/sounds/fail.mp3'],
            volume: 0.25,
        });
        Resources.onReady(() => {
            this.start();
        });
        Resources.load([
            '/img/sprites.png',
        ]);
    }

    measureFPS() {
        let now = Date.now();
        State.frameCount++;
        State.delta = (now - State.then);
        if (State.delta > 1000) {
            State.fps = State.frameCount;
            info.displayFPS();
            State.frameCount = 0;
            State.then = now;
            State.timer--;
            if (State.timer <= 0) {
                this.grid.enemies.push(new Enemy(this.grid, [Math.round(this.grid.w / 2), 2], [1, 0]));
                this.grid.enemies.push(new Enemy(this.grid, [Math.round(this.grid.w / 2), 2], [-1, 0]));
                State.timer = 30;
            }
        }
    }

    loop() {
        if (!State.ended) {
            if (!State.paused) {
                this.grid.paint();
                this.player.update();
                this.player.paint();
                this.grid.enemies.forEach((enemy) => {
                    enemy.update();
                    enemy.paint();
                });
                this.qix.update();
                this.qix.draw();
                this.measureFPS();
                this.grid.displayScore();
                this.grid.displayTimer();
                requestAnimationFrame(this.loop);
            }
        } else {
            State.soundtrack.pause();
            if (State.won) {
                State.gameWon.play();
                this.grid.ctx.font = "150px Georgia";
                this.grid.ctx.strokeStyle = 'white';
                this.grid.ctx.lineWidth = 8;
                this.grid.ctx.strokeText("Victory!", 80, 280);
                this.grid.ctx.fillStyle = 'green';
                this.grid.ctx.fillText("Victory!", 80, 280);
            } else {
                State.gameLost.play();
                this.grid.ctx.font = "150px Georgia";
                this.grid.ctx.strokeStyle = 'white';
                this.grid.ctx.lineWidth = 8;
                this.grid.ctx.strokeText("Game", 140, 200);
                this.grid.ctx.strokeText("Over", 175, 350);
                this.grid.ctx.fillStyle = 'red';
                this.grid.ctx.fillText("Game", 140, 200);
                this.grid.ctx.fillText("Over", 175, 350);
            }
        }
    }

    sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async start() {
        State.soundtrack.play();
        this.onKeydown = this.onKeydown.bind(this);
        this.onKeyup = this.onKeyup.bind(this);
        document.addEventListener("keydown", this.onKeydown);
        document.addEventListener("keyup", this.onKeyup);
        this.grid.ctx.font = "30px Arial";
        this.grid.ctx.clearRect(this.grid.offset[0] + this.grid.width * 1.4, this.grid.offset[1] + this.grid.height * 1.3, this.grid.width / 5, this.grid.height / 3);
        this.grid.ctx.fillText("3", this.grid.offset[0] + this.grid.width * 1.5, this.grid.offset[1] + this.grid.height * 1.5);
        await this.sleep(1000);
        this.grid.ctx.clearRect(this.grid.offset[0] + this.grid.width * 1.4, this.grid.offset[1] + this.grid.height * 1.3, this.grid.width / 5, this.grid.height / 3);
        this.grid.ctx.fillText("2", this.grid.offset[0] + this.grid.width * 1.5, this.grid.offset[1] + this.grid.height * 1.5);
        await this.sleep(1000);
        this.grid.ctx.clearRect(this.grid.offset[0] + this.grid.width * 1.4, this.grid.offset[1] + this.grid.height * 1.3, this.grid.width / 5, this.grid.height / 3);
        this.grid.ctx.fillText("1", this.grid.offset[0] + this.grid.width * 1.5, this.grid.offset[1] + this.grid.height * 1.5);
        await this.sleep(1000);
        this.loop();
    }

    toggle() {
        if (this.paused) {
            this.paused = false;
            this.loop();
        } else {
            this.paused = true;
        }
    }

    disableAll() {
        const keys = this.keys;
        keys.shift = false;
        keys.up = false;
        keys.down = false;
        keys.right = false;
        keys.left = false;
    }

    onKeydown(event) {
        const keys = State.keys;
        this.disableAll();
        keys.shift = event.shiftKey;
        switch (event.keyCode) {
            case 37:
                event.preventDefault();
                keys.left = true;
                break;
            case 38:
                event.preventDefault();
                keys.up = true;
                break;
            case 39:
                event.preventDefault();
                keys.right = true;
                break;
            case 40:
                event.preventDefault();
                keys.down = true;
                break;
            case 112:
                this.toggle();
                break;
            case 80:
                this.toggle();
                break;
            default:
        }
    }

    onKeyup(event) {
        const keys = State.keys;
        switch (event.keyCode) {
            case 37:
                event.preventDefault();
                keys.left = false;
                break;
            case 38:
                event.preventDefault();
                keys.up = false;
                break;
            case 39:
                event.preventDefault();
                keys.right = false;
                break;
            case 40:
                event.preventDefault();
                keys.down = false;
                break;
            default:
        }
    }

}